<template>
  <div>
    <a-modal v-model="dialog_addMeeting" centered title="จองห้องประชุม">
      <a-spin :spinning="spinning">
        <div class="spin-content">
          <a-form :label-col="{ span: 5 }" :wrapper-col="{ span: 12 }">
            <a-form-item label="วันที่และเวลาที่ท่านเลือกจองห้องประชุม">
              <a-alert
                :message="this.message_date_alert"
                :description="this.message_time_alert"
              />
            </a-form-item>

            <a-form-item label="โปรดเลือกห้องประชุมด้านล่างนี้">
              <a-select
                placeholder="โปรดเลือกห้องประชุม"
                option-filter-prop="children"
                :filter-option="filterOption"
                @change="selected_room"
              >
                <a-select-option
                  inputReadOnly
                  :value="items"
                  v-for="items in List_room_select"
                  :key="items"
                >
                  {{ items }}
                </a-select-option>
              </a-select>
            </a-form-item>

            <a-form-item label="หัวข้อการประชุม:">
              <a-input
                v-model="Topic_Meeting"
                v-decorator="[
                  'agenda',
                  {
                    rules: [
                      { required: true, message: 'โปรดระบุหัวข้อการประชุม' },
                    ],
                  },
                ]"
                placeholder="โปรดระบุหัวข้อการประชุม"
              />
            </a-form-item>

            <a-form-item label="เชิญผู้ร่วมประชุม:">
              <a-select
                mode="multiple"
                placeholder="ค้นหาผู้ร่วมประชุม"
                @change="Change_Select_person"
              >
                <a-select-option
                  v-for="items in data_select_person"
                  :key="items"
                >
                  {{ items }}
                </a-select-option>
              </a-select>
            </a-form-item>
          </a-form>
        </div>
      </a-spin>
      <template slot="footer">
        <a-button key="back" @click="CloseDialog()">
          ยกเลิก
        </a-button>
        <a-button
          key="submit"
          type="primary"
          :loading="loading"
          @click="Validate_Before_API_SendData()"
        >
          จอง
        </a-button>
      </template>
    </a-modal>
  </div>
</template>
<script>
import Swal from "sweetalert2";

const formItemLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4 },
};
const formTailLayout = {
  labelCol: { span: 2 },
  wrapperCol: { span: 4, offset: 2 },
};
export default {
  components: {},
  data: () => ({
    dialog_addMeeting: false,
    List_room_select: null,
    spinning: false,
    data_select_person: null,
    message_date_alert: "",
    message_time_alert: "",
    Topic_Meeting: "",
    person_select: "",
    formItemLayout,
    formTailLayout,
    username: "",
    room_Meeting: "",
    meeting_start: "",
    date_not_convert_select: "",
    meeting_end: "",
    loading: false,
    room_value: null,
    num_room: "",
  }),
  mounted() {},
  methods: {
    open(data) {
      // console.log(data);
      this.message_date_alert = "วันที่ : " + data.date_convert_select;
      this.message_time_alert =
        "เวลาที่เริ่มต้น : " +
        data.start_time +
        " น." +
        " - " +
        "เวลาที่สิ้นสุด : " +
        data.end_time +
        " น.";
      this.date_not_convert_select = data.date_not_convert_select;
      (this.meeting_start = data.start_time),
        (this.meeting_end = data.end_time),
        (this.List_room_select = data.room_source_api);
      this.dialog_addMeeting = true;
      this.API_GetUserTable();
    },
    CloseDialog(e) {
      console.log(e);
      this.dialog_addMeeting = false;
    },
    selected_room(value) {
      console.log(value);
      if (value != "") {
        this.num_room = value;
      }
    },
    onSearch(value) {
      console.log(value);
    },
    Change_Select_person(value) {
      console.log(value);

      if (value != "") {
        this.person_select = value;
      }
    },
 
    API_GetUserTable() {
      this.spinning = true;
      this.axios({
        method: "GET",
        url: this.$store.state.url_get_API_axios + "api/v1/userTable/email",
        // headers: { Authorization: "Bearer " + localStorage.token },
      })

        .then((response) => {
          console.log(response.data);
          if (response.data.Status == "Query Successful") {
            this.data_select_person = response.data.Value;
            this.spinning = false;
          } else {
            this.spinning = false;
            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          this.spinning = false;
          // console.log(error.message);
          // this.loading = false;
          if (error.message == "Request failed with status code 400") {
            this.$message.error("ไม่พบข้อมูล");
          }
        });
    },
       Validate_Before_API_SendData() {
      // console.log(this.num_room);
      // console.log(this.Topic_Meeting);
      // console.log(this.person_select);
      // console.log(this.date_not_convert_select);
      // console.log(this.meeting_start);
      // console.log(this.meeting_end);

      if (
        this.num_room == "" ||
        this.Topic_Meeting == "" ||
        this.person_select == [] ||
        this.date_not_convert_select == "" ||
        this.meeting_start == "" ||
        this.meeting_end == ""
      ) {
       this.$error(
         {
         
              title: "กรุณากรอกข้อมูลให้ครบถ้วน",
              content: "โปรดตรวจสอบข้อมูลของท่าน",
            });
      } else {
        this.API_SendAddData();
      }
    },
    API_SendAddData() {
      this.spinning = true;

      var source = {
        one_email: "nitchakarn.ho@one.th",
        agenda: this.Topic_Meeting,
        room_num: this.num_room,
        guests: this.person_select,
        meeting_start: this.date_not_convert_select + " " + this.meeting_start,
        meeting_end: this.date_not_convert_select + " " + this.meeting_end,
      };
      console.log(source);

      this.axios({
        method: "POST",
        url: this.$store.state.url_get_API_axios + "api/v1/booking",
        data: source,
        // headers: { Authorization: "Bearer " + localStorage.token },
      })

        .then((response) => {
          console.log(response.data);
          if (response.data.Status == "Booking Successful") {
            this.spinning = false;
            this.dialog_addMeeting = false
            this.$success({
              centered,
              title: "จองห้องประชุมสำเร็จ",
              content: 'ท่านสามารถดูการจองของท่านได้จากเมนู ดูการจอง',
            });
          } else {
            this.spinning = false;
            this.dialog_addMeeting = false
            this.$error({
              title: "ไม่พบข้อมูล",
              content: "ไม่พบข้อมูล",
            });
          }
        })
        .catch((error) => {
          this.spinning = false;
          this.dialog_addMeeting = false
          console.log(error.message);
          // this.loading = false;
          // if (error.message == "Request failed with status code 400") {
          //   this.$message.error("ไม่พบข้อมูล");
          // }
        });
    },
  },
};
</script>
